export const doTrackingTagsChoosePlan = (planType, selectedPlanData) => {
  switch (planType) {
    case 'essential': {
      fbTag('PPS2 - ButtonClick - Travel - ChoosePlan_BuyNow-Essential', { Product: 'Travel', Click_Text: 'BuyNow' });
      gaEvent('Travel - BuyNowEssential', {
        event_category: 'Travel',
        event_label: 'Button - Travel - BuyNowEssential',
      });
      ampTag('TravelBuyNowA', getAmpTags(selectedPlanData, 'essential'));
      break;
    }
    case 'elite': {
      fbTag('PPS2 - ButtonClick - Travel - ChoosePlan_BuyNow-Elite', { Product: 'Travel', Click_Text: 'BuyNow' });
      gaEvent('Travel - BuyNowElite', {
        event_category: 'Travel',
        event_label: 'Button - Travel - BuyNowElite',
      });
      ampTag('TravelBuyNowA', getAmpTags(selectedPlanData, 'elite'));
      break;
    }
    default: {
      fbTag('PPS2 - ButtonClick - Travel - ChoosePlan_BuyNow-Basic', { Product: 'Travel', Click_Text: 'BuyNow' });
      gaEvent('Travel - BuyNowBasic', {
        event_category: 'Travel',
        event_label: 'Button - Travel - BuyNowBasic',
      });
      ampTag('TravelBuyNowA', getAmpTags(selectedPlanData, 'basic'));
      break;
    }
  }
};

